	.btn-primary {
		color: $white !important;
		background-color: #f66347;
		border-color: #f66347;
		box-shadow: 0 0px 10px -5px rgba(0, 98, 205, 0.5);
		margin-right: 3px;

		&:hover {
			color: $white !important;
			background-color: #f8694e;
			border-color: #f8694e;
		}

		&:focus,
		&.focus {
			box-shadow: 0 0 0 2px rgba(0, 98, 205, 0.5);
			background-color: #f8694e;
			border-color: #f8694e;
		}

		&.disabled,
		&:disabled {
			color: $white;
			background-color: #f8694e;
			border-color: #f8694e;
		}

		&:not(:disabled):not(.disabled) {
			&:active,
			&.active {
				color: $white;
				background-color: #f8694e;
				border-color: #f8694e;
			}
		}
	}

	.show > .btn-primary.dropdown-toggle {
		color: $white;
		background-color: #f8694e;
		border-color: #f8694e;
	}

	.btn-primary:not(:disabled):not(.disabled) {
		&:active:focus,
		&.active:focus {
			box-shadow: 0 0 0 2px rgba(0, 98, 205, 0.1);
		}
	}

	.show > .btn-primary.dropdown-toggle:focus {
		box-shadow: 0 0 0 2px rgba(0, 98, 205, 0.1);
	}

	.btn-secondary {
		color: $white !important;
		background-color: $secondary;
		border-color: $secondary;
		box-shadow: 0 0px 10px -5px rgba(251, 28, 82, 0.5);
		margin-right: 3px;

		&:hover {
			color: $white !important;
			background-color: #f83e6b;
			border-color: #f83e6b;
		}

		&:focus,
		&.focus {
			box-shadow: 0 0 0 2px rgba(251, 28, 82, 0.1);
			background-color: #f83e6b;
			border-color: #f83e6b;
		}

